import { LanguageToolClient, LanguageToolClientImpl } from "./client";
import { RulesConfig } from "./types";
import { getStorageItem, setStorageItem } from "../../utils/localStorage";
import { config } from "../../config/corrector";
import rfdc from "rfdc";
import { ConfigProps, Variants } from "../../config/types";

export type LanguageToolProps = {
    server?: string
    client?: LanguageToolClient
}

interface UserConfig {
    variant: Variants
    preferences: Map<string,string|boolean>
}

const globalRulesConfig : RulesConfig = {
    enabledRules: [],
    disabledRules: [ "ESPAI_FI", "ADVERBIS_MENT", "TOO_LONG_SENTENCE", "WHITESPACE_RULE"],
    enabledCategories: [],
    disabledCategories: []
}

// const _emptyRulesConfig = () : RulesConfig => {
//     return {
//         enabledRules: [],
//         disabledRules: [],
